export default {
	data() {
		return {
			tabitems: [
				'收入',
				'支出'
			],
			current: 0,
			activeColor: '#17393c',
			styleType: 'text',
			add_list: [{
				name: '积分增加',
				type: '签到',
				add: '+10',
				date: '2018-10-10'
			}],
			reduce_list: [{
				name: '积分减少',
				type: '活动',
				reduce: '-10',
				date: '2018-10-10'
			}]
		};
	},
	methods: {
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		}
	},
	components: {
		segmentedControl
	}
}



